import React, { useRef, useState, useEffect} from 'react'
import { instance } from '../../api/axios';
import useOnClickOutside from '../../hooks/useOnClickOutside';
import Spinner from '../../assets/icons/Spin-1s-200px.svg';
import './modal.css'

// interface modalProps {
//   addr1:string;
//   dist:string;
//   firstimage:string;
//   title:string;
//   setModalOpen?:any;
//   tel?:string;
// }


const Modal =({addr1, dist, contentid, firstimage, title, setModalOpen}) => {
  const [overview, setOverview] = useState('')
  const [loading, setLoading] = useState(false)
  const distRound = Math.round(Number(dist));
  const ref = useRef();
  console.log('ref', ref.current);
  useOnClickOutside(ref, () => {
  setModalOpen(false);
  })



  const getRes = async () => {
    try {
      setLoading(true)
      const paramDetail = {
        contentId: contentid,
        defaultYN: 'Y',
        addrinfoYN: 'Y',
        overviewYN: 'Y',
        MobileOS: 'ETC',
        MobileApp: 'AppTest'
      }
      const getContId = await instance.get('detailCommon', {params:paramDetail})
      const overview = getContId.data.response.body.items.item[0].overview;
      setOverview(overview)

    } catch (err){
      console.log(err);
    }
    setLoading(false)
  }

  useEffect(() => {
    getRes()
  }, [])

  const textLengthCut = (str:string, n:number) => {
    return str?.length > n ? str.substring(0, n) +'...' : str
  }

  
  return (
    <div className='whole' role="presentation">
      <div className='modal_wrap'>
        <div className="modal">
          <span 
            className="modal_close"
            onClick={() => setModalOpen(false)}
          >
            X
          </span>

          <img
            className='modal_img' 
            src={`${firstimage}`}
            alt={ title }
          />

          <div className='modal_content'>
            <h2>: { title }</h2>
            <ul>
              <li><span className='bold'></span>: { addr1 }</li>
              <li><span className='bold'>  </span>: { distRound }m</li>
              {loading? <img src={Spinner} alt='loading' className='overview_loading'/> : overview ? <li><span className='bold'> </span><br/> { textLengthCut(overview, 100) }</li> : <p className='no_overview'>   .</p>
              }
            </ul>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Modal

